import {applyCurrentFilter, updateClearCompletedButton, updateItemsLeft} from "./info";

let todoIdCounter = 0;

export function renderList(): HTMLElement {
	const list = document.createElement('ul');
	list.id = 'todo-list';
	return list;
}

export function setupInputHandler() {
	const input = document.getElementById('todo-input') as HTMLInputElement;
	const list = document.getElementById('todo-list') as HTMLUListElement;

	if (input && list) {
		input.addEventListener('keydown', (event) => {
			if (event.key === 'Enter') {
				const value = input.value.trim();
				if (value !== '') {
					const li = document.createElement('li');
					li.className = 'todo-item';
					li.dataset.id = (todoIdCounter++).toString(); // 등록 순서 저장

					const span = document.createElement('span');
					span.textContent = value;
					span.className = 'todo-text';

					const deleteButton = document.createElement('button');
					deleteButton.textContent = '삭제';
					deleteButton.className = 'delete-button';

					li.addEventListener('click', (e: MouseEvent): void => {
						if ((e.target as HTMLElement).tagName === 'BUTTON') {
							return;
						}

						span.classList.toggle('completed');
						const isCompleted = span.classList.contains('completed');

						if (isCompleted) {
							console.log("todo 완료 토글");
						} else {
							console.log("todo 미완료 토글");
						}

						sortList();
						applyCurrentFilter();
						updateItemsLeft();
						updateClearCompletedButton();
					});

					deleteButton.addEventListener('click', () => {
						li.remove();
						console.log("todo 삭제")
						updateItemsLeft();
						updateClearCompletedButton();
					});

					li.appendChild(span);
					li.appendChild(deleteButton);
					list.prepend(li);

					input.value = '';

					console.log("todo 등록")

					updateItemsLeft();
					updateClearCompletedButton();
				}
			}
		});
	}
}

export function sortList(): void {
	const list = document.getElementById('todo-list') as HTMLUListElement;
	const items = Array.from(list.children) as HTMLElement[];

	const activeItems = items
		.filter(item => {
			const span = item.querySelector('span');
			return span && !span.classList.contains('completed');
		})
		.sort((a, b) => {
			const idA = Number(a.dataset.id);
			const idB = Number(b.dataset.id);
			return idB - idA; // id 기준 내림차순
		});

	const completedItems = items.filter(item => {
		const span = item.querySelector('span');
		return span && span.classList.contains('completed');
	});

	list.innerHTML = '';

	activeItems.forEach(item => list.appendChild(item));
	completedItems.forEach(item => list.appendChild(item));
}

let draggingItem: HTMLElement | null = null;
let guideLine: HTMLDivElement | null = null;
let isDragging = false;

export function setupDragAndDrop(): void {
	const list = document.getElementById('todo-list') as HTMLUListElement;

	list.addEventListener('mousedown', (e) => {
		const target = e.target as HTMLElement;

		// 버튼 눌렀을 때는 드래그 시작하면 안 됨
		if (target.tagName === 'BUTTON') return;

		// 정확히 li 요소 찾아
		const li = target.closest('li') as HTMLElement;
		if (!li) return;

		// 완료된 아이템이면 드래그 금지
		const span = li.querySelector('span');
		if (span?.classList.contains('completed')) {
			return;
		}

		draggingItem = li;
		isDragging = true;

		guideLine = document.createElement('div');
		guideLine.style.position = 'absolute';
		guideLine.style.width = '4px';
		guideLine.style.height = `${draggingItem.offsetHeight}px`;
		guideLine.style.backgroundColor = 'limegreen';
		guideLine.style.zIndex = '1000';
		guideLine.style.left = '0'; // li 왼쪽
		guideLine.style.pointerEvents = 'none'; // 드래그 막지 않게

		const listRect = list.getBoundingClientRect();
		const itemRect = li.getBoundingClientRect();
		guideLine.style.top = `${itemRect.top - listRect.top + list.scrollTop}px`;

		list.appendChild(guideLine);

		// ✅ 여기서 등록해야 드래그 움직임/드랍 처리 가능
		document.addEventListener('mousemove', onMouseMove);
		document.addEventListener('mouseup', onMouseUp);
	});
}



function onMouseMove(e: MouseEvent): void {
	if (!isDragging || !draggingItem || !guideLine) return;

	const list = document.getElementById('todo-list') as HTMLUListElement;
	const items = Array.from(list.children) as HTMLElement[];

	let targetItem: HTMLElement | null = null;

	for (const item of items) {
		const rect = item.getBoundingClientRect();
		if (e.clientY > rect.top && e.clientY < rect.bottom) {
			targetItem = item;
			break;
		}
	}

	if (targetItem) {
		const rect = targetItem.getBoundingClientRect();
		const listRect = list.getBoundingClientRect();

		guideLine.style.top = `${rect.top - listRect.top + list.scrollTop}px`;
	}
}


function onMouseUp(e: MouseEvent): void {
	if (!isDragging || !draggingItem || !guideLine) return;

	const list = document.getElementById('todo-list') as HTMLUListElement;
	const items = Array.from(list.children) as HTMLElement[];

	let insertBeforeItem: HTMLElement | null = null;

	for (const item of items) {
		const rect = item.getBoundingClientRect();
		if (e.clientY > rect.top && e.clientY < rect.bottom) {
			insertBeforeItem = item;
			break;
		}
	}

	if (insertBeforeItem && insertBeforeItem !== draggingItem) {
		const span = insertBeforeItem.querySelector('span');
		if (span && !span.classList.contains('completed')) {
			list.insertBefore(draggingItem, insertBeforeItem);
		}
	}

	isDragging = false;
	draggingItem = null;
	guideLine?.remove();
	guideLine = null;

	document.removeEventListener('mousemove', onMouseMove);
	document.removeEventListener('mouseup', onMouseUp);
}
